import React from "react";
import { ZliveForm } from "zerod";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/ZliveForm/doc.md";
import formData from "./formData";
import linkages from "./linkages";
import { Button } from "antd";
export default AmdDocHOC(doc, {
	demo0: function() {
		class Myjavascript extends React.PureComponent {
			formData = { ...formData };
			onSave = newFormData => {
				console.log(newFormData);
			};
			render() {
				return <ZliveForm formData={this.formData} onSave={this.onSave} />;
			}
		}
		const Dom = AshowDemoHOC(<Myjavascript />, true);
		return <Dom />;
	},
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			state = {
				formValue: {
					dogCard: "",
				},
			};
			formData = { ...formData };
			onSubmit = values => {
				console.log(values);
				return Promise.resolve();
			};
			componentDidMount() {
				// setTimeout(() => {
				// 	this.setState({
				// 		formValue:{
				// 			dogName:"8888"
				// 		}
				// 	})
				// }, 2000);
			}
			render() {
				return (
					<ZliveForm.FormViewer
						formValues={this.state.formValue}
						formData={this.formData}
						onSubmit={this.onSubmit}
						momentFormat={true}
						submitBtnRender={submit => {
							return (
								<div className="z-text-center">
									<Button type="primary" onClick={submit}>
										
									</Button>
								</div>
							);
						}}
					/>
				);
			}
		}
		const Dom = AshowDemoHOC(<Myjavascript />, true);
		return <Dom />;
	},
	demo2: function() {
		class Myjavascript extends React.PureComponent {
			render() {
				return <span />;
			}
		}
		return <Myjavascript />;
	},
});
